/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */

article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}

body {
	line-height: 1;
}

ol, ul {
	list-style: none;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

html,
body {
	height: 100%;
}

html {
	overflow: hidden;
	font-size: 100%;
	box-sizing: border-box;
}

*,
*:before,
*:after {
	box-sizing: inherit;
}

body {
	overflow-y: scroll;
	-webkit-overflow-scrolling: touch;
	line-height: 1.4;
	font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
	background: #fff;
}

h1,
h2,
h3,
h4,
h5,
h6,
b,
strong {
	font-weight: 700;
}

img {
	display: block;
	max-width: 100%;
	width: 100%;
}

.app {
	min-height: 100%;
	
	background: #f5f5f5
}

.app__inner {
	max-width: 1312px;
	max-width: 82rem;
	margin: auto;
	padding: 16px 16px 32px;
	padding: 1rem 1rem 2rem;
}

.app__title {
	font-size: 32px;
	font-size: 2rem;
	padding: 16px 0;
	padding: 1rem 0;
}

.app__page {
	display: none;
}

.app__page_visible {
	display: block;
}

.button {
	display: table;
	margin: 8px 0;
	margin: .5rem 0;
	padding: 0 16px;
	padding: 0 1rem;
	line-height: 32px;
	line-height: 2rem;
	font-size: 16px;
	font-size: 1rem;
	white-space: nowrap;
	background: #ffdc61;
	outline: 0;
	border: 0;
	border-top: 3px solid;
	border-top: 3px solid transparent;
	border-bottom: 3px solid rgba(0, 0, 0, .1);
	border-radius: 3px;
	cursor: pointer;
	-webkit-transition: box-shadow .2s ease;
	transition: box-shadow .2s ease
}

.button:hover,
	.button:focus {
	box-shadow: 0 0 16px #ffdc61;
}

.button:active {
	border-top-color: rgba(0, 0, 0, .1);
	border-bottom-color: transparent;
}

.input {
	padding: 8px 0 16px;
	padding: .5rem 0 1rem
}

.input__label {
	display: block;
	padding: 0 0 8px;
	padding: 0 0 .5rem;
	color: #888;
	cursor: pointer;
}

.input__field {
	display: block;
	width: 100%;
	padding: 0 8px;
	padding: 0 .5rem;
	line-height: 32px;
	line-height: 2rem;
	background: #fff;
	border: 1px solid rgba(0, 0, 0, .2);
	-webkit-transition: border-color .1s, box-shadow 1s;
	transition: border-color .1s, box-shadow 1s;
}

.input__field:focus,
		.input__field:active {
	outline: 0;
	border-color: #ffdc61;
	box-shadow: inset 0 2px 4px rgba(0, 0, 0, .1);
}

.player {
	position: relative;
	width: 100%;
	padding-bottom: 56.25%;
	overflow: hidden;
	color: #fff;
	background: #000
}

.player:hover {}

.player:hover .player__controls {
	opacity: 1;
	-webkit-transform: translate(0, 0);
	        transform: translate(0, 0);
}

.player_initialized {}

.player_initialized::before {
	display: none;
}

.player_initialized .player__controls {
	visibility: visible;
}

.player::before {
	content: '';
	position: absolute;
	top: calc(50% - 1.5rem);
	left: calc(50% - 1.5rem);
	width: 48px;
	width: 3rem;
	height: 48px;
	height: 3rem;
	border-radius: 50%;
	border: 3px solid;
	border-color: transparent #fff;
	-webkit-animation: spin .7s infinite linear;
	        animation: spin .7s infinite linear;
}

.player__stage {
	position: absolute;
	z-index: 1;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
}

.player__canvas {
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	max-width: 100%;
	max-height: 100%;
	width: auto;
	height: auto;
}

.player__controls {
	position: absolute;
	z-index: 10;
	bottom: 0;
	left: 0;
	right: 0;
	visibility: hidden;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	padding: 0 16px;
	padding: 0 1rem;
	height: 48px;
	height: 3rem;
	background: rgba(0, 0, 0, .5);
	opacity: 0;
	-webkit-transform: translate(0, 100%);
	        transform: translate(0, 100%);
	-webkit-transition: opacity 1.2s ease, -webkit-transform .4s ease;
	transition: opacity 1.2s ease, -webkit-transform .4s ease;
	transition: transform .4s ease, opacity 1.2s ease;
	transition: transform .4s ease, opacity 1.2s ease, -webkit-transform .4s ease;
	will-change: opacity, transform;
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden;
}

@media screen and (max-width: 79.9375rem) {

	.player__controls {
		-webkit-transform: translate(0, 0);
		        transform: translate(0, 0);
		opacity: 1;
	}
}

.player__control {
	margin-right: 16px;
	margin-right: 1rem;
	font-size: 0;
	color: transparent;
	background: transparent;
	border: 0;
	outline: 0;
	cursor: pointer;
}

.player__control_state {
	width: 24px;
	width: 1.5rem;
	height: 24px;
	height: 1.5rem;
	background-size: cover;
}

